@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
.searchInputs {
  display: flex;
}

.searchContainer input {
  background-color: #f9f9f9;
  border: 0;
  border-radius: 0px 5px 5px 0px;
  font-size: 18px;
  width: 60vw;
  padding: 10px;
  margin-right: 10px;
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
  padding: 10px;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  border-radius: 8px;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.13);
}

.searchIcon {
  width: 35px;
  background-color: #f9f9f9;
  display: grid;
  place-items: center;
  border-radius: 5px 0px 0px 5px;
}

input:focus {
  outline: none;
}
.searchIcon svg {
  font-size: 26px;
}

.card-div {
  width: 150px;
  height: 150px;
  background-color: white;
  padding: 15px;
  margin-bottom: 20px;
  text-align: center;
  border-radius: 8px;
}
.card-div:hover {
  cursor: pointer;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
}

.card-image img {
  height: 75px;
}

.card-image {
  padding-top: 10px;
}

.card-title {
  padding-bottom: 10px;
}

/* import lato font from web */

/* Body is the parent most element.. these attributes get passed to ALL children  */
body {
  font-family: Lato;
  background-color: #f2f2f2;
}
button {
  /* Get rid of all the default borders for buttons */
  decoration: none;
  border-decoration: none;
  border: none;
}
button:hover {
  cursor: pointer;
}

